import React from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { animations } from '../../constants/animations';

interface CompositeAnimationProps {
  isVisible: boolean;
}

// Styled Components
const Container = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`;

const MainContainer = styled.div`
  margin: 2rem;
  background-color: #f9fafb;
  border-radius: 0.5rem;
  border: 2px dashed #e5e7eb;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr auto;
  grid-template-areas:
    "layers composition"
    "layers final";
  gap: 2rem;
  padding: 2rem;
  height: calc(100% - 4rem);

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template-areas:
      "layers"
      "composition";
    gap: 0;
    padding-bottom: 5rem;
  }
`;

const LayerStack = styled.div`
  grid-area: layers;
  position: relative;
  min-height: 20rem;
`;

interface LayerCardProps {
  color: string;
}

const LayerCard = styled.div<LayerCardProps>`
  height: 12rem;
  width: 100%;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  padding: 1rem;
  background: ${props => props.color};
  border: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(4px);
  transform: rotateX(10deg) rotateY(5deg);
`;

const LayerTitle = styled.div`
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
`;

const LayerDescription = styled.div`
  font-size: 0.875rem;
  color: #4b5563;
`;

const CompositionPanel = styled(motion.div)`
  grid-area: composition;
`;

const CompositionContent = styled.div`
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  padding: 1.5rem;
`;

const CompositionTitle = styled.h3`
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
`;

const CompositionSteps = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1rem;
`;

const StepItem = styled.div`
  display: flex;
  align-items: center;
  gap: 0.75rem;
`;

interface StepIndicatorProps {
  bgColor: string;
}

const StepIndicator = styled.div<StepIndicatorProps>`
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 9999px;
  background-color: ${props => props.bgColor};
`;

const StepText = styled.div`
  font-size: 0.875rem;
  color: #4b5563;
`;

const FinalDisplay = styled(motion.div)`
  grid-area: final;
  background: linear-gradient(to bottom right, #eef2ff, #eff6ff);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid #e0e7ff;
  align-self: end;
  justify-self: end;
  max-width: 80%;
`;

const FinalDisplayText = styled.div`
  font-size: 0.875rem;
  color: #4338ca;
`;

const GpuHint = styled(motion.div)`
  position: absolute;
  bottom: -1rem;
  right: 1rem;
  background-color: white;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #e5e7eb;
  z-index: 50;
`;

const GpuHintContent = styled.div`
  font-size: 0.875rem;
  color: #474747;
`;

const GpuHintTitle = styled.div`
  font-weight: 500;
  margin-bottom: 0.5rem;
`;

const GpuHintList = styled.ul`
  list-style-type: disc;
  list-style-position: inside;
  font-size: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
`;

const CompositeAnimation: React.FC<CompositeAnimationProps> = ({ isVisible }) => {
  const { t } = useTranslation();
  const timeline = animations.steps.composite.timeline;

  // Layer information
  const layers = [
    {
      id: 'background',
      label: t('animations.composite.layers.background'),
      description: t('animations.composite.descriptions.background'),
      color: 'linear-gradient(to bottom right, rgba(219, 234, 254, 0.8), rgba(239, 246, 255, 0.8))',
      z: 10,
    },
    {
      id: 'content',
      label: t('animations.composite.layers.content'),
      description: t('animations.composite.descriptions.content'),
      color: 'linear-gradient(to bottom right, rgba(237, 233, 254, 0.8), rgba(245, 243, 255, 0.8))',
      z: 20,
    },
    {
      id: 'ui',
      label: t('animations.composite.layers.ui'),
      description: t('animations.composite.descriptions.ui'),
      color: 'linear-gradient(to bottom right, rgba(220, 252, 231, 0.8), rgba(240, 253, 244, 0.8))',
      z: 30,
    }
  ];

  return (
    <Container>
      <MainContainer>
        {/* Layer Stack */}
        <LayerStack>
          {layers.map((layer, index) => (
            <motion.div
              key={layer.id}
              initial={{ opacity: 0, y: -20 }}
              animate={isVisible ? { 
                opacity: 1, 
                y: index * 40,
                z: layer.z
              } : { 
                opacity: 0, 
                y: -20,
                z: layer.z
              }}
              transition={{ 
                delay: timeline.backgroundLayer.start + index * 0.2,
                duration: 0.3
              }}
              style={{ 
                position: 'absolute',
                left: 0,
                top: 0,
                width: '100%',
                perspective: '1000px' 
              }}
            >
              <LayerCard color={layer.color}>
                <LayerTitle>{layer.label}</LayerTitle>
                <LayerDescription>{layer.description}</LayerDescription>
              </LayerCard>
            </motion.div>
          ))}
        </LayerStack>

        {/* Composition Process */}
        <CompositionPanel
          initial={{ opacity: 0 }}
          animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
          transition={{ delay: timeline.compositing.start, duration: timeline.compositing.duration }}
        >
          <CompositionContent>
            <CompositionTitle>{t('animations.composite.steps.title')}</CompositionTitle>
            
            {/* Composition Steps */}
            <CompositionSteps>
              <StepItem>
                <StepIndicator bgColor="#60a5fa" />
                <StepText>{t('animations.composite.steps.createLayers')}</StepText>
              </StepItem>
              <StepItem>
                <StepIndicator bgColor="#c084fc" />
                <StepText>{t('animations.composite.steps.rasterize')}</StepText>
              </StepItem>
              <StepItem>
                <StepIndicator bgColor="#4ade80" />
                <StepText>{t('animations.composite.steps.gpuAcceleration')}</StepText>
              </StepItem>
              <StepItem>
                <StepIndicator bgColor="#f9fafb" />
                <StepText>{t('animations.composite.steps.complete')}</StepText>
              </StepItem>
            </CompositionSteps>
          </CompositionContent>
        </CompositionPanel>

     

        {/* GPU Hint - Positioned relative to the container */}
        <GpuHint
          initial={{ opacity: 0 }}
          animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
          transition={{ delay: timeline.gpuHint.start, duration: timeline.gpuHint.duration }}
        >
          <GpuHintContent>
            <GpuHintTitle>{t('animations.composite.gpuHints.title')}</GpuHintTitle>
            <GpuHintList>
              <li>{t('animations.composite.gpuHints.transform')}</li>
              <li>{t('animations.composite.gpuHints.willChange')}</li>
              <li>{t('animations.composite.gpuHints.layerUsage')}</li>
            </GpuHintList>
          </GpuHintContent>
        </GpuHint>
      </MainContainer>
    </Container>
  );
};

export default CompositeAnimation;
